<template>
  <div class="ex1">
    <div class="app-sec">
      <div class="app-col">
        <h2>Example 1: With Transform</h2>
        <p>Basic content scroll in horizontal dimension with <code>transform</code> property.</p>
        <pre data-highlight>
let viewport = document.querySelector('.ex1 .tbl-main')
let content = viewport.querySelector('table')

let sb = new ScrollBooster({
  viewport, // this parameter is required
  content, // scrollable element
  mode: 'x', // scroll only in horizontal dimension
  onUpdate: (data)=> {
    // your scroll logic goes here
    content.style.transform = `translateX(${-data.position.x}px)`
  }
})</pre>
      </div>
      <div class="app-col">
        <app-table></app-table>
      </div>
    </div>
  </div>
</template>

<script>
import ScrollBooster from 'scrollbooster'
import AppTable from './Table.vue'

export default {
  name: "app-example1",
  components: { AppTable },
  mounted() {
    initScroll()
  }
}

function initScroll() {
  let viewport = document.querySelector('.ex1 .tbl-main')
  let content = viewport.querySelector('table')

  let sb = new ScrollBooster({
    viewport,
    content,
    mode: 'x',
    onUpdate: (data)=> {
      content.style.transform = `translateX(${-data.position.x}px)`
    }
  })
}

</script>

<style lang="less">
</style>



