<template>
  <div id="app" class="app">
    <app-header></app-header>
    <app-example1></app-example1>
    <app-example2></app-example2>
    <app-example3></app-example3>
    <app-footer></app-footer>
  </div>
</template>

<script>
import Prism from 'prismjs'

import AppHeader from './Header.vue'
import AppExample1 from './Example1.vue'
import AppExample2 from './Example2.vue'
import AppExample3 from './Example3.vue'
import AppFooter from './Footer.vue'

export default {
  name: "app",
  components: { AppHeader, AppExample1, AppExample2, AppExample3, AppFooter },
  mounted() {
    handleHighlight()
  }
}

function handleHighlight () {
  let hl = document.querySelectorAll('[data-highlight]')
  for (let i = 0; i < hl.length; i++) {
    let content = hl[i].innerText
    let html = Prism.highlight(content, Prism.languages.javascript)
    hl[i].innerHTML = html
  }
}
</script>

<style lang="less">
.app {
  padding: 50px;
  font: 16px 'Raleway', Helvetica, sans-serif;
  max-width: 1980px;
  margin: auto;

  &-sec {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 60px;
  }

  &-col {
    padding: 20px;
    flex: 1 1 50%;
    min-width: 400px;
  }
}

[data-highlight] {
  padding: 10px;
  font-size: 14px;
  background: #FAFAFA;
  border: 1px solid rgb(233, 233, 233);
  white-space: pre-wrap;
  border-radius: 3px;
}

code {
  padding: 2px 5px;
  border-radius: 3px;
  background: #fafafa;
  border: 1px solid rgb(233, 233, 233);
}

h2 {
  margin-top: 0;
}
</style>



