<style lang="less">
.footer {
  text-align: center;
  padding: 40px 0;
  &-btn {
    display: inline-flex;
    align-items: center;
    padding: 12px 18px;
    text-decoration: none;
    background: #1cb4dc;
    border-radius: 5px;
    color: #fff;
    font-weight: bold;
    font-size: 26px;
    transition: background .3s;
    img {
      max-height: 36px;
      margin-right: 18px;
      filter: brightness(100);
