<template>
  <div class="tbl">
    <div class="tbl-left">
      <table class="table">
        <thead class="thead">
          <tr>
            <th>#</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(row, ind) in rows">
            <td>{{ind + 1}}</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div class="tbl-main-wrap">
      <div class="tbl-main">
        <table class="table">
          <thead class="thead">
            <tr>
              <th v-for="col in rows[0].cols">
                {{col.caption}}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(row, ind) in rows">
              <td v-for="col in row.cols">
                {{col.val}}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="tbl-right">
      <table class="table">
        <thead class="thead">
          <tr>
            <th>#</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(row, ind) in rows">
            <td><input type="checkbox"></td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
let generateData = () => {
  let rows = []
  let words = ["first", "UI", "scroll", "drag", "dude", "boost", "bounce", "wow"]
  for (let i = 0; i < 8; i++) {
    let cols = []
    for (let j = 0; j < 15; j++) {
      cols.push({
        caption: words[Math.round((words.length - 1) * Math.random())],
        val: words[Math.round((words.length - 1) * Math.random())] + ' ' + words[Math.round((words.length - 1) * Math.random())]
      })
    }
    rows.push({ cols })
  }
  return rows
}

let data = generateData()

export default {
  name: "app-table",
  data() {
    return {
      rows: data
    }
  }
}
</script>

<style lang="less">
.table {
  margin: 0;
  td {
    padding: 8px;
    background: #fff;
  }
}

.thead {
  th {
    padding: 15px 8px;
    background: #ffab92;
    color: #9c523c;
  }
}

.tbl {
  display: flex;
  border: 1px solid #eee;
  background: #e4e4e4;
  box-shadow: 0 4px 10px rgba(0,0,0,.06);
  border-radius: 5px;
  overflow: hidden;
  &-main-wrap {
    flex: 1;
    position: relative;
    min-width: 0;
  }
  &-main {
    width: 100%;
    overflow: hidden;
    cursor: move;
    td {
      white-space: nowrap;
      background: #f4f4f4;
      min-width: 140px;
    }
    th {
      background: #ffa083;
      text-align: left;
    }
  }
  &-left,
  &-right {
    td {
      padding: 8px 20px;
    }
  }
}
</style>



