<template>
  <div class="ex3">
    <div class="app-sec">
      <div class="app-col">
        <h2>Example 3: Vertical And Horizontal Scroll</h2>
        <p>Both horizontal and vertical scroll are supported. There is also a mode that emulates scroll via mouse wheel.</p>
        <pre data-highlight>
let viewport = document.querySelector('.ex3 .img-wrapper')
let content = viewport.querySelector('.img-main')
let img = content.querySelector('img')

let sb = new ScrollBooster({
  viewport,
  content,
  emulateScroll: true,
  onUpdate: (data)=> {
    content.style.transform = `translate(
      ${-data.position.x}px,
      ${-data.position.y}px
    )`
  }
})

img.addEventListener('load', () => {
  // update sizes/metrics after image load
  sb.updateMetrics()

  // set viewport position to the center of an image
  let offsetX = img.scrollWidth - viewport.offsetWidth
  let offsetY = img.scrollHeight - viewport.offsetHeight
  sb.setPosition({
    x: offsetX / 2,
    y: offsetY / 2
  })
})</pre>
      </div>
      <div class="app-col">
        <div class="img-wrapper">
          <div class="img-main">
            <img src="./assets/pexels-photo-590478.jpg" alt="">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import img from './assets/pexels-photo-940365.jpg'
import ScrollBooster from 'scrollbooster'

export default {
  name: "app-example3",
  data() {
    return {}
  },
  mounted() {
    initScroll()
  }
}

function initScroll() {
  let viewport = document.querySelector('.ex3 .img-wrapper')
  let content = viewport.querySelector('.img-main')
  let img = content.querySelector('img')

  let sb = new ScrollBooster({
    viewport,
    content,
    emulateScroll: true,
    onUpdate: (data)=> {
      content.style.transform = `translate(
        ${-data.position.x}px,
        ${-data.position.y}px
      )`
    }
  })

  img.addEventListener('load', () => {
    sb.updateMetrics()

    let offsetX = img.scrollWidth - viewport.offsetWidth
    let offsetY = img.scrollHeight - viewport.offsetHeight
    sb.setPosition({
      x: offsetX / 2,
      y: offsetY / 2
    })
  })
}

</script>

<style lang="less">
.img-wrapper {
  // max-width: 500px;
  height: 340px;
  overflow: hidden;
  margin: auto;
  border: 3px solid #ffab92;
  background: #ffab92;
  border-radius: 6px;
  box-shadow: 0 2px 10px rgba(0,0,0,.1);
  cursor: move;
}

.img-main img {
  display: block;
}
</style>



