<template>
  <div class="header">
    <div class="header-logo">
      <div class="header-logo-abbr">SB</div>
      <canvas id="canvas"></canvas>
    </div>
    <a class="header-link" href="https://github.com/ilyashubin/scrollbooster">
      <h1 class="header-caption">ScrollBooster</h1>
    </a>
    <h2 class="header-sub">Enjoyable content drag-to-scroll library</h2>
  </div>
</template>

<script>
export default {
  name: "app-header",
  mounted() {
    makeLogo()
  }
}

function makeLogo() {
  let wrapper = document.querySelector('.header-logo')
  let canvas = document.querySelector('#canvas')
  let c = canvas.getContext('2d')

  let width = canvas.width = wrapper.offsetWidth
  let height = canvas.height = wrapper.offsetHeight
  let centerW = Math.round(width / 2)
  let centerH = Math.round(height / 2)

  let offset = 10

  function roundToNearest (val, nearest) {
    return Math.round(val / nearest) * nearest
  }

  class Particle {
    constructor() {
      this.x = Math.random() * width
      this.y = this.getY()
      console.log('y:', this.y)
      this.w = 20 + Math.round(Math.random() * 50)
      this.h = 3
    }

    getY() {
      return roundToNearest(offset + (Math.random() * (height - offset)), Math.round(height / 6))
    }

    move () {
      this.x -= 2
      if (this.x + this.w < 0) {
        this.x = width + Math.round(Math.random() * 150)
        this.y = this.getY()
      }
    }
  }

  let particles = []
  particles = Array.apply(null, Array(15)).map(()=> {
    return new Particle()
  })

  let animate = () => {
    c.clearRect(0,0,width,height)
    c.fillStyle = '#fff'
    c.fillStyle = '#1cb4dc'

    particles.forEach(p => {
      c.fillRect(p.x, p.y, p.w, p.h)
      p.move()
    })

    requestAnimationFrame(animate)
  }
  animate()
}
</script>

<style lang="less">
.header {
  text-align: center;
  margin-bottom: 100px;
  &-logo {
    position: relative;
    width: 300px;
    height: 120px;
    background: #ffab92;
    margin: auto auto 20px;
    overflow: hidden;
    border-radius: 4px;
    &:before {
      content: '';
      position: absolute;
      top: 5px;
      left: 50%;
      transform: translateX(-50%);
      width: 180px;
      height: calc(100% - 10px);
      border-radius: 3px;
      box-shadow: 0 0 0 100px rgba(255,255,255,.8);
    }
    &-abbr {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 66px;
      font-weight: bold;
      color: #fafafa;
      letter-spacing: .05em;
      text-shadow: 0 2px #ffa083, 0 -2px #ffa083, -2px 0 #ffa083, 2px 0 #ffa083;
    }
  }
  &-link {
    text-decoration: none;
    color: #1cb4dc;
    transition: color .1s;
    &:hover {
      color: lighten(#1cb4dc, 10%);
    }
  }
  &-caption {
    font-size: 44px;
    margin-bottom: .3em;
    text-decoration: none;
  }
  &-sub {
    color: #aaa;
    font-size: 20px;
  }
}
</style>




